.substitute {
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: 0.5s;

  img {
    margin-bottom: 0;
  }

  div {
    position: relative;
  }

  p {
    color: #fff;
    background-color: rgba(#000, 0.5);
    padding: 0 1rem;
    line-height: 2;
    border-radius: 1rem;
    font-size: 0.9rem;
  }

  button {
    $color: #e40320;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    width: 1.15rem;
    height: 1.15rem;
    border-radius: 50%;
    background-color: $color;
    display: none;

    &:hover {
      background-color: darken($color, 5);
    }
  }

  &:hover {
    button {
      display: flex;
    }
  }
}

.substitutes-container {
  padding-bottom: 2rem;
  display: grid;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  overflow-y: auto;
  height: 300px;
}